import { combineReducers } from 'redux';
import { ADD_TICKET, DEL_TICKET } from '../actions/actionTypes';
import Input from '../components/Input';

const initialState = {
    tickets: [<Input key={0} />]
};

function tickets(state = initialState.tickets, action) {
    switch (action.type) {
        case ADD_TICKET:
            return [
                ...state,
                action.ticket
            ];
        case DEL_TICKET:
        return Object.assign({}, state, {
            tickets: [
                state,
                action.ticket
            ]
        });
        // console.log(state);
        // return state.splice(-1, 1);
        default:
            return state;
    }
}

export default combineReducers({
    tickets,
});

export const getTickets = (state) => {
    return state.tickets;
};